import { Component, OnInit,OnDestroy } from '@angular/core';
import { AppService }       from './app.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [AppService],
})
export class AppComponent implements OnInit, OnDestroy {
  title : string =  'Crawl http://www.scheels.com';
  items = [];
  connection;
  siteURL : string = '/shop/scheels-catalog/-men/-mens-activities-/mens-workwear/Carhartt-Adult-Acrylic-Watch-Cap-35481-A18-L3';
  
  constructor(private appService:AppService) {}

  crawlSite(){
    this.appService.crawlSite(this.siteURL);
    // this.siteURL = '';
  }

  ngOnInit() {
    this.connection = this.appService.getItems().subscribe(item => {
      this.items.push(item);
    })
  }
  
  ngOnDestroy() {
    this.connection.unsubscribe();
  }
}


